@import url(https://fonts.googleapis.com/css2?family=Cabin&family=Lobster&family=Urbanist&display=swap);
html * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Urbanist", sans-serif;
  /* font-family: 'Cabin', sans-serif; */
}

@media only screen and (min-width: 375px) {
  html {
    font-size: 4vw;
  }

  header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 0.5rem;
    align-items: center;
    z-index: 1;
  }

  header > div:first-child {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 5rem;
  }

  header > div > .goBackPH {
    width: 2rem;
    height: 2rem;
    margin: 0 2.1rem;
  }

  header > div a svg {
    color: grey;
    margin: 0 2.1rem;
  }

  header div a {
    text-decoration: none;
    display: flex;
    align-items: center;
  }

  header div a img{
    display: none;
  }

  header div a h1 {
    color: #808080;
    margin-bottom: 0.5rem;
    cursor: pointer;
  }

  header #admin,
  a#adminForm {
    font-size: 1.2rem;
    display: none;
    color: hsl(0, 0%, 55%);
    padding: 0.2rem 1rem;
    text-decoration: none;
  }
  header #admin:hover,
  header a#adminForm:hover {
    border-bottom: 0.2px solid rgba(0, 0, 0, 0.253);
  }

  header>a.active {
    border-bottom: 0.2px solid rgba(0, 0, 0, 0.253);
  }

  .swiperBox {
    width: 100%;
    height: 100%;
  }

  .swiper {
    width: 100%;
    height: 100%;
    color: #000;
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
  }

  .swiperSlide {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
  }

  .swiper-button-next,
  .swiper-button-prev {
    color: white !important;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

