@media only screen and (min-width: 768px) {
  html {
    font-size: 2vw;
  }
  /* width */
  ::-webkit-scrollbar {
    width: 1rem;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #8d8d8d;
    box-shadow: 1px 1px 10px black;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #792c00;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #792c00;
  }
  header div a h1 {
    color: rgb(129, 129, 129);
    font-size: 1.5rem;
  }
  header {
    background-color: #ffffff;
    width: 100%;
    height: 3.5rem;
    display: flex;
    justify-content: space-between !important;
    padding-top: 0.5rem;
    align-items: center;
    z-index: 1;
    box-shadow: 1px 1px 10px #0404045e;
  }

  header div a img{
    height: 7rem;
    display: block;
  }
