@media only screen and (min-width: 375px) {
  .containerHome {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  .containerHome .containerHero {
    width: 100%;
    height: 115vh;
    background-image: url(/static/media/background375-2.f62adc29.jpg);
    /* background-image: url("../assets/backGround375.jpg") */
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /* FILTER DE HOME */
  .containerHome .homeFilter {
    margin-top: 7rem;
    width: 100%;
    height: 16rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .containerHome .homeFilter > * {
    font-size: 1.2rem;
    width: 95%;
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    font-weight: 500;
  }

  /* .containerHome .homeFilter .firstRow {

  } */

  .containerHome .homeFilter .firstRow ul {
    list-style-type: none;
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .containerHome .homeFilter .firstRow li {
    background-color: black;
    color: white;
    padding: 0.3rem;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
  }

  .containerHome .homeFilter .firstRow li.active {
    background-color: white;
    color: black;
    box-shadow: 10px 10px 31px -1px rgba(0, 0, 0, 0.47);
  }

  /* flechita indicadora */
  .containerHome .homeFilter .firstRow li.active::before {
    content: "";
    width: 0;
    height: 0;
    border-left: 0.7rem solid transparent;
    border-right: 0.7rem solid transparent;
    border-top: 0.7rem solid #fff;
    position: absolute;
    top: 1.9rem;
    left: calc((100% / 2) - 0.7rem);
  }

  .containerHome .homeFilter .secondRow {
    width: 94%;
    margin-top: 0.6rem;
    display: flex;
    flex-direction: column;
    align-self: center;
    align-items: center;
    background-color: rgb(255 255 255 / 55%);
    border-radius: 0.5rem;
    box-shadow: 10px 10px 31px -1px rgba(0, 0, 0, 0.47);
    -webkit-backdrop-filter: blur(14.5px);
            backdrop-filter: blur(14.5px);
  }

  .containerHome .homeFilter .secondRow div {
    width: 100%;
    display: flex;
  }

  .containerHome .homeFilter .secondRow select {
    font-size: 1.1rem;
    background-color: transparent;
    border: none;
    flex: 1 1;
    height: 2.5rem;
  }

  .containerHome .homeFilter .secondRow select:focus {
    outline: 1px solid transparent;
  }
  .containerHome .homeFilter .boxes {
    background-color: transparent;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.5rem;
    padding-left: 1.8rem;
  }

  .containerHome .homeFilter .boxes > div {
    width: 100%;
    display: flex;
    align-items: center;
  }

  .containerHome .homeFilter .boxes input {
    margin-right: 0.4rem;
  }

  .containerHome .homeFilter .homeFilterButton {
    width: 100%;
    visibility: visible;
  }

  .containerHome .homeFilter .homeFilterButtonBigResponsive {
    visibility: hidden;
  }

  .containerHome .homeFilter .homeFilterButton button {
    width: 90%;
    height: 2.5rem;
    background-color: black;
    color: white;
    font-size: 1.4rem;
    padding: 0.2rem;
    border: none;
    border-radius: 0.3rem;
    cursor: pointer;
  }
}

